import React  from "react";
import { HashRouter as Router } from "react-router-dom";
import Nav from "./Nav";
import UserProvider from "../../providers/UserProvider";
import Application from "./Application";



function MainApp() {

  return (
    <div className="App">
      <Router>
        <Nav />
        <UserProvider>
          <Application/>
        </UserProvider>
      </Router>
    </div>
  );
}

export default MainApp;