import React, { useState, useEffect} from 'react';

export default function useGetResultHtml(aspectObjs, results) {
    const [resultHtml, setResultHtml] = useState([]);

    useEffect(() => {
        // console.log("useGetResultHtml triggered")
        let tempResult = [];
        tempResult.push(
            <div key="head">
           <div key="headtext" className="mb-3"> &Delta; P shows the difference in probability that the aspect is true and the 
               probability that the aspect is not true
           </div>
           </div>
        )
        // console.log('all aspectObj is: '+JSON.stringify(aspectObjs))
        aspectObjs.forEach((aspectObj,i)=>{
            let aspectDescr = aspectObj.aspectDescr;
            let oddsFavor = [];
            let oddsAgainst = [];
            // console.log('aspectObj is: '+JSON.stringify(aspectObj));
            if(aspectObj.combis){
                // console.log('combi if entered')
            aspectObj.combis.forEach(combi=>{
                // console.log('combi: '+JSON.stringify(combi));

                    const checkFValue = (result=>{
                        let passesAllFilters = true;
                        combi.favorFilters.forEach(favFilter=>{
                            // console.log('forEach favFilter: '+ JSON.stringify(favFilter))
                    // console.log('result in forEach: '+ JSON.stringify(result));
                    // console.log('Result[favFilter.fName: '+result[favFilter.fName])
                    if(!(favFilter.fValue===result[favFilter.fName]&&result.hasOwnProperty(favFilter.fName))){
                        // console.log("set passesAllFilters favors to false")
                        passesAllFilters = false;
                    }
                })
                    return(passesAllFilters)
                    })
                    let tempFavors = results.filter(checkFValue);
                    tempFavors.forEach(favor=>{
                        // console.log(' forEach favor '+JSON.stringify(favor) )
                        if(oddsFavor.indexOf(favor)===-1){
                            oddsFavor.push(favor);
                        }
                    })
                    // console.log("oddsFavor list length: "+JSON.stringify(oddsFavor.length))

                               
                    const checkAValue = (result=>{
                        let passesAllFilters = true;
                        combi.againstFilters.forEach(againstFilter=>{
                        // console.log('forEach againstFilter: '+ JSON.stringify(againstFilter))
                        // console.log('result in forEach: '+ JSON.stringify(result))
                        if(!(againstFilter.fValue===result[againstFilter.fName]&&result.hasOwnProperty(againstFilter.fName))){
                            passesAllFilters=false;
                        }
                    })
                        return(passesAllFilters)
                    })
                    let tempAgainst = results.filter(checkAValue);
                    tempAgainst.forEach(against=>{
                        // console.log(' forEach against '+JSON.stringify(against) )
                        if (oddsAgainst.indexOf(against)===-1){
                            oddsAgainst.push(against);
                        }
                    })
                    // console.log("oddsAgainst list length: "+JSON.stringify(oddsAgainst.length))
                })
        }
        // let aspectOdds = 1;
        let prob = 0;
        if (oddsAgainst.length>0||oddsFavor.length>0){
        // aspectOdds = oddsFavor.length/oddsAgainst.length
        
        prob = 2*(oddsFavor.length/(oddsFavor.length+oddsAgainst.length))-1;
        }
       let graphProb = Math.abs(prob*50);
       let clNames = graphReverse(prob);
       let barClasses = getBarClasses(prob)

                tempResult.push(
                <div key={`graphs ${i}`} className="pb-2">
                    {/* Aspect: {aspectName} <br/> */}
                    <span key={`asp-desc ${i}`} className="text-secondary">{aspectDescr}</span><br/>
                    <div key={`clNames ${i}`} className={clNames}>
                        {/* { console.log('oddsAgainst.length: '+JSON.stringify(oddsAgainst.length))} */}
                            {/* {console.log('oddsFavor.length: '+JSON.stringify(oddsFavor.length))} */}
                    <div key={`pg-bar-pt1 ${i}`} className="progress-bar bg-light-grey" role="progressbar" style={{width: 50+"%"}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                    {prob===0&&<div key={`pg-bar-pt2 ${i}`} class="progress-bar bg-teal" role="progressbar" style={{width: 2 +"px"}} aria-valuenow="30" aria-valuemin="0" aria-valuemax="100"></div>}
<div key={`pg-bar-pt3 ${i}`} className={barClasses} role="progressbar" aria-valuenow={prob} aria-valuemin="0" aria-valuemax="100" style={{width:graphProb+"%"}}>
    {(prob>=0.2||prob<=-0.2)&&<span key={`pg-title ${i}`} className="sr-only">&Delta; P = {prob.toFixed(2)}</span>}
    </div>
  </div>
  {(prob<0.2&&prob>-0.2)&&<div key={`pg-desc ${i}`}>&Delta; P = {prob.toFixed(2)}</div>}
  <span key={`pg-desc2 ${i}`}>(Chosen {(100*prob).toFixed(0)}% {prob<0 ? "less": "more"} than the opposite.)</span>

               </div>)
                // console.log('resultHtml: '+JSON.stringify(tempResult))
                setResultHtml(tempResult);
            })
            if(aspectObjs.length===0){
                setResultHtml([<div><h5>No aspects defined.</h5></div>])
            }
              return 
    }, [aspectObjs, results])


   const graphReverse = (prob)=>{
        let tempClNames = "progress w-80 mx-auto"
        if (prob<0){
            tempClNames="progress w-80 mx-auto flex-row-reverse"
        }
        // console.log("clNames: "+tempClNames);
        return  (tempClNames);
    }

    const getBarClasses = (prob)=>{
        let barClasses = "progress-bar bg-teal progress-bar-striped";
        if (prob<0){
            barClasses = "progress-bar bg-purple progress-bar-striped"
        }
        return barClasses;
    }


    return {resultHtml};
}
