import { useState, useEffect } from "react";
import { pemFirestore } from "../../firebase/config";


const useGetAspectList = (userId, dceId, showAspectsFlag)=>{
const [aspects, setAspects] = useState([]);

    useEffect(() => {
        if(userId&&dceId){
        let response = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList');
        response.get()
        .then((data)=>{
            let tempDocs = [];
        // let tempNameList = [];
        data.docs.forEach(doc => {
            tempDocs.push({...doc.data(), id: doc.id});
            // tempNameList.push(doc.data().name.dceName);
          });
        //   console.log('Aspects data: '+JSON.stringify(tempDocs));
        setAspects(tempDocs) ;
        // setDceNameList(tempNameList);
        }).catch((err)=>{
            console.log('Error with loading document'+err);
        })
    }
    }, [userId, dceId, showAspectsFlag]);
    return {aspects}
}

export default useGetAspectList;